@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
}

.banner {
  height: 92vh;
  color: white;
  background-image: url(/React-API-Recipe/static/media/hero.975f2d30.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.banner .hero-text {
  width: 480px;
  position: absolute;
  top: 30%;
  left: 60%;
}

.banner .hero-text h1 {
  font-family: "Poppins", sans-serif;
  font-size: 55px;
  font-weight: 700;
}

.banner .hero-text p {
  font-family: "Poppins", sans-serif;
  margin-top: 20px;
}

.navbar .nav-item {
  margin-left: 20px;
  font-weight: bold;
}

.featured {
  margin-left: 100px;
  width: 100%;
}

.featured img {
  border-radius: 50%;
  width: 200px;
  height: 200px;
}

.searchForm {
  margin-left: 450px;
}

.compButton {
  position: absolute;
  top: 80%;
  left: 60%;
  width: 20%;
  padding: 10px;
  border: none;
  border-radius: 7px;
  font-size: 20px;
  background-color: #0d6efd;
  color: white;
}

@media only screen and (max-width: 360px) {
  .featured {
    margin-left: 0px !important;
    width: 100%;
  }
  .featured img {
    border-radius: 50%;
    width: 200px;
    height: 200px;
  }
  .banner {
    height: 50vh;
    color: white;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  .banner .hero-text {
    width: 100%;
    position: absolute;
    top: 18%;
    left: 0%;
  }
  .banner .hero-text h1 {
    font-family: "Poppins", sans-serif;
    font-size: 25px;
    text-align: center;
  }
  .banner .hero-text p {
    font-size: 10px;
    text-align: center;
  }
  .banner .hero-text form {
    width: 50%;
    margin: auto;
    margin-top: 15px;
  }
}
